import React, { Component } from 'react';
import { withRouter, Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { actionCreators } from './store';
import { Layout, Menu } from 'antd';
import { DashboardOutlined,
    FileDoneOutlined,
    SmileOutlined,
    SettingOutlined,
    MenuUnfoldOutlined,
    MenuFoldOutlined
} from '@ant-design/icons';
import "./style.css";

const { Header, Sider, Content, Footer } = Layout;
const { SubMenu } = Menu;

class MainLayout extends Component {
    render() {
        const { collapsed, toggle } = this.props;
        const defaultSelectedKeys = [this.props.location.pathname];
        const defaultOpenKeys = ['/'+ this.props.location.pathname.split('/')[1]];
        console.log('layout');

        return (
            <Layout style={{ minHeight: '100vh' }}>
                <Sider
                    width="208px"
                    style={{ boxShadow: "2px 0 6px rgba(0,21,41,.35)" }}
                    trigger={null}
                    collapsible
                    collapsed={this.props.collapsed}
                >
                    <div className="logo">{!collapsed ? '运营宝典' : '运营'}</div>
                    <Menu
                        theme="dark"
                        mode="inline"
                        defaultSelectedKeys={defaultSelectedKeys}
                        defaultOpenKeys={defaultOpenKeys}
                    >
                        <Menu.Item
                            key="/dashboard"
                        >
                            <Link to="/dashboard">
                                <DashboardOutlined />
                                <span>我的面板</span>
                            </Link>
                        </Menu.Item>
                        <SubMenu
                            key="/libs"
                            title={
                                <span>
                                    <FileDoneOutlined />
                                    <span>运营宝典</span>
                                </span>
                            }
                        >
                            <Menu.Item
                                key="/libs/tool"
                            >
                                <Link to="/libs/tool">
                                    <SmileOutlined />
                                    <span>工具集</span>
                                </Link>
                            </Menu.Item>
                        </SubMenu>
                    </Menu>
                </Sider>
                <Layout className="site-layout">
                    <Header style={{ padding: 0, background: '#fff', boxShadow: "2px 0 6px rgba(0,21,41,.35)" }}>
                        {React.createElement(collapsed ? MenuUnfoldOutlined : MenuFoldOutlined, {
                            className: 'trigger',
                            onClick: toggle
                        })}
                    </Header>
                    <Content
                        style={{
                            margin: '16px 16px 0 16px',
                            minHeight: 280,
                        }}
                    >
                        <>{this.props.children}</>
                    </Content>
                    <Footer style={{ textAlign: 'center' }}>&#169; 2020 苏州抓马生物科技有限公司</Footer>
                </Layout>
            </Layout>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        collapsed: state.getIn(['layout', 'collapsed'])
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        toggle() {
            dispatch(actionCreators.toggle());
        }
    }
};

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(MainLayout));